* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: roboto, sans-serif;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 60px;
  background: #fff;
  border-bottom: 1px solid #ddd;
}
section.cover {
  padding-top: 60px;
  background: #efefef;
  height: 100vh;
}
section.cover h1 {
  text-align: center;
  margin-top: 3rem;
}
section.intro {
  padding: 3rem 0;
  background: #fff;
}
section.intro p {
  width: 100%;
  max-width: 666px;
  margin: 20px auto;
  padding: 0 20px;
}
article {
  position: relative;
  height: 1500px;
}
section.tabellone {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  align-content: center;
  width: 100%;
  margin: 3rem auto;
  background: #fff;
  transition: all 0.6s linear;
}
section.tabellone.stretto {
  width: calc(100% - 600px);
}
section.tabellone div a {
  display: block;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
}
section.tabellone > div {
  display: block;
  background: red;
  border: 10px solid white;
  width: 280px;
  height: 280px;
  cursor: pointer;
}
section.voci {
  position: fixed;
  top: 60px;
  left: 0;
  display: none;
  width: 600px;
  height: 100vh;
  background: #fff;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
section.voci > div {
  display: block;
  width: 100%;
  min-height: 500px;
  border-top: 1px solid black;
  padding: 3rem;
}
